.expenses-container {
  display: flex;
  height: 100vh;
}

.expenses {
  padding: 1rem;
  /* background: rgba(11, 17, 33, 0.35); */
  background: rgba(255, 255, 255, 0.04);
  box-shadow: inset 4px 4px 10px 0 rgba(255, 255, 255, 0.2),
    -6px -6px 10px 0 rgba(11, 17, 33, 1);
  margin: 1.5rem 1rem;
  width: 50vw;
  /* max-width: 100%; */
  height: 80%;
}

@media (max-width: 1380px) {
  .expenses {
    height: 75%;
  }
}

@media (max-width: 1280px) {
  .expense-item__price {
    font-size: 1rem;
    padding: 0.4em 1.4rem;
  }
}

@media (max-width: 1180px) {
  .expenses {
    width: 48vw;
  }
}

